@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Faster+One&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Myeongjo:wght@800&display=swap");
$font-family: "Noto Sans KR", "Helvetica Neue", sans-serif !default;

// 컬러
$color-white: #fff;
$color-blue: #1e78d0;
$color-orange: #f4580b;
$color-brand: #f9c700;
$color-brand2: #08c4f2;
$color-brand3: #8a7eff;
$color-brand4: #f1574d;
$color-level1: #52a4e0;
$color-level2: #5fa089;
$color-level3: #cb7e66;
$color-level4: #a663ca;
$color-kakaopay: #f7c705;
$color-brand4-alpha70: #e4e4e4;
$color-font: #1f1f1f !default;
$color-font-light: #cfcfcf;
$color-bg: #fff;
$color-bg-alpha80: #e4e4e4be;
$color-bg-invert-alpha80: #1d1e20a8;
$color-header-bg: #403c43;

$color-header-bg2: #49454c;
$popup-background-color: #1d1e20c2;

// 폰트 컬러
$font-color: #d0d0d0 !default;

$font-size-h: 2.6rem; // 대타이틀
$font-size-tit: 2.2rem; // 일반적인 타이틀
$font-size-subtit: 1.9rem; // 작은타이틀
$font-size-typo: 1.6rem; // 일반적인 본문크기
$font-size-typos: 1.3rem;

$play-control-height: 6rem;
$header-height: 5rem;
$controler-height: 4rem;

// $safe-area-top-a: constant(safe-area-inset-top);
// $safe-area-top-b: env(safe-area-inset-top);
$safe-area-bottom-a: constant(safe-area-inset-bottom);
$safe-area-bottom-b: env(safe-area-inset-bottom);

html {
    font-size: 62.5%;
    // opacity: 0.1;
}

body {
    font-size: 1.6em;
    line-height: 1.6;
    font-weight: 500;
    letter-spacing: -0.5;
    font-family: $font-family;
    color: $color-font;
    background-color: $color-bg;
    scroll-behavior: smooth;
    &.noscroll {
        overflow: hidden;
        width: 100%;
        height: 100%;
    }
}

.App {
    overflow: hidden;
}
.page-container {
    transition: margin-left 0.6s cubic-bezier(0.08, 0.915, 0.365, 1);
    width: 100vw;
    overflow: hidden;
}
#gaiframe {
    opacity: 0;
}

i {
    &.i-sort {
        .txt {
            margin-left: 5px;
        }
    }
    .txt {
        font-family: $font-family;
        // font-weight: 400;
        //
    }
}
.i-label {
    font-family: $font-family;
}

.view-vv {
    width: 100%;
    display: inline-block;
    position: relative;
}
.view-Alert {
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: #e4e4e4d0;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    .AlertNoticeInapp {
        width: 75vw;
        background-color: $color-bg;
        padding: 10px;
        border: 15px solid $color-kakaopay;
        border-radius: 5px;
        text-align: center;

        .message {
            font-size: 14px;
            font-weight: bold;
            margin-bottom: 15px;
        }
        .btn-confirm {
            display: block;
            width: 100%;
            background-color: $color-brand4;
            color: #ffe5e3;
            border: 0;
            font-size: 15px;
            font-weight: bold;
            padding: 5px;
            border-radius: 3px;
            margin-bottom: 5px;
        }
        .btn-close {
            display: block;
            width: 100%;
            background-color: transparent;
            color: #8a8a8a;
            border: 0;
            font-size: 14px;
            font-weight: bold;
            padding: 5px;
            border-radius: 3px;
        }
    }
