                font-size: 24px;
                font-weight: bold;
                letter-spacing: -1px;
            }
            .btn-close {
                // float: right;
                position: absolute;
                right: 0;
                top: 0;
                font-size: 24px;
            }
        }
        .btn-create-qr {
            cursor: pointer;
            position: relative;
            width: 128px;
            .cover {
                position: absolute;
                width: 128px;
                height: 128px;
                font-size: 50px;
                top: 25px;
                // left: 10px;
            }
            .dummy {
                opacity: 0.4;
            }
        }
        .message {
            font-size: 14px;
            font-weight: bold;
            margin-bottom: 15px;
        }
        .btn-confirm {
            display: block;
            width: 100%;
            background-color: $color-brand4;
            color: #ffe5e3;
            border: 0;
            font-size: 15px;
            font-weight: bold;
            padding: 5px;
            border-radius: 3px;
            margin-bottom: 5px;
        }
