        //     color: #8a8a8a;
        //     border: 0;
        //     font-size: 14px;
        //     font-weight: bold;
        //     padding: 5px;
        //     border-radius: 3px;
        // }
    }
}
.btn {
    background-color: transparent;
    border: 0;
    &.btn-block {
        display: block;
    }
    &.btn-blank {
        padding: 0;
    }
    .i-bullet {
        margin-right: 5px;
    }
    .i-right {
        margin-left: 10px;
    }
    &.btn-border {
        border: 1px solid #000;
        margin-right: 10px;
        font-weight: bold;
        border-radius: 3px;
    }
}

