.view-Home {
    .tab-section {
        margin-top: 110px;
        padding: 5px 15px;
        .func-group {
            height: 40px;
            padding-top: 10px;
            .btn-sort {
                background-color: #cbcbcb;
                display: inline-block;
                padding: 4px 7px;
                border-radius: 5px;
                float: right;
                i {
                    strong {
                        margin-left: 5px;
                    }
                }
            }
        }
    }

    .header-fixed {
        position: fixed;
        background-color: $color-header-bg;
        z-index: 100;
        width: 100%;
        .tabs-scroll {
            width: 100vw;
            overflow-x: scroll;
            &::-webkit-scrollbar {
                display: none;
            }
        }
        .header-func-group {
            height: 30px;
        }
        .tabs {
            // margin-top: 30px;
            padding: 0 15px;
            font-size: 15px;
            font-weight: bold;
            // border-bottom: 4px solid #2d2d2f;
            display: inline-flex;
            width: 450px;
            min-width: 100%;

            .btn-tab {
                &:first-child {
                    margin-left: 0;
                }
                // display: inline-block;
                transition: color 0.5s;
                margin: 0 15px;
                color: rgb(124, 124, 124);
                color: #999999;
                .lang {
                    color: #ffffffcc;
                    background-color: #da7971;
                    border-radius: 20px;
                    padding: 1px 3px 2px;
                    min-width: 20px;
                    text-align: center;
                    display: inline-block;
                    margin-left: 3px;
                    opacity: 0.6;
                    font-size: 11px;
                }

                &.active {
                    font-size: 17px;
                    color: #fff;
                    border-bottom: 5px solid #e0e0e0;
                    padding-bottom: 5px;
                    // margin-bottom: 10px;
                    .lang {
                        opacity: 1;
                    }
                }
            }
        }
    }

    .topic-group {
        // margin-top: 50px;
        &:last-child {
            margin-bottom: 50px;
        }
        &.level1 {
            .day {
                color: $color-level1;
            }
        }
        &.level2 {
            .day {
                color: $color-level2;
            }
        }
        &.level3 {
            .day {
                color: $color-level3;
            }
        }
        &.level4 {
            .day {
                color: $color-level4;
            }
        }
        .topic-header {
            padding: 40px 0;
        }
        .title {
            font-size: 30px;
            font-weight: bold;
        }
        .btn-guide {
            font-size: 25px;
            float: right;
        }
        .guide-message {
            border: 1px solid #c8c8c8;
            background-color: $color-bg;
            position: relative;
            padding: 0 0 10px;
            border-radius: 2px;
            .guide-header {
                height: 20px;
                // padding: 5px;
                padding-top: 5px;
                padding-right: 5px;
            }
            .btn-close {
                float: right;
            }
            .script {
                padding: 10px 20px;
            }
        }
        .topic-title {
            display: flex;
            align-items: center;
            margin-bottom: 10px;
            font-size: 20px;
            // color: #fff;
            font-weight: 600;
            .i-history {
                margin-right: 10px;
            }
            .i-right {
                margin-left: 20px;
            }
        }
        .item-topic2 {
            box-sizing: border-box;
            // padding: 5px 10px;
            border-radius: 2px;
            overflow: hidden;
            margin-right: 20px;
            display: inline-block;
            width: 100%;
            min-height: 150px;
            // color: #fff;
            // background-color: #1D474F;
            // border: 5px solid;
            font-size: 14px;
            line-height: 18px;
            position: relative;
            border-bottom: 1px solid rgb(187, 187, 187);
            .mark-inapp {
                position: absolute;
                font-size: 20px;
                opacity: 0.9;
                // left: 52px;
                right: 10px;
                top: 25px;
                color: #000;
            }
            .img {
                position: absolute;
                width: 150px;
                height: 150px;
                // opacity: 0.1;
            }
            .info {
                padding: 25px 10px;
                line-height: 22px;
                .day {
                    font-size: 30px;
                    font-weight: bold;
                    margin-bottom: 20px;
                    margin-top: 5px;
                }
                .desc {
                    font-size: 13px;
                    font-weight: bold;
                    opacity: 0.5;
                    margin-bottom: 5px;
                }
                .title-ko {
                    font-size: 20px;
                    font-weight: bold;
                }
                .title-en {
                    font-size: 20px;
                    opacity: 0.7;
                    margin-bottom: 10px;
                    font-weight: bold;
                }
            }
        }
    }

    .HistoryList {
        .title {
            margin: 20px 10px;
            opacity: 0.8;
            font-size: 22px;
            // font-weight: bold;
        }
    }

    .HistoryItem {
        box-sizing: border-box;
        // padding: 5px 10px;
        border-radius: 2px;
        overflow: hidden;
        margin-right: 20px;
        display: inline-block;
        width: 100%;
        // min-height: 150px;
        // color: #fff;
        // background-color: #1D474F;
        // border: 5px solid;
        font-size: 14px;
        line-height: 18px;
        position: relative;
        border-bottom: 1px solid rgb(187, 187, 187);
        .mark-inapp {
            position: absolute;
            font-size: 20px;
            opacity: 0.9;
            // left: 52px;
            right: 10px;
            top: 25px;
            color: #000;
        }
        .img {
            position: absolute;
            width: 150px;
            height: 150px;
            // opacity: 0.1;
        }
        .info {
            padding: 0 10px;
            line-height: 22px;
            .day {
                font-size: 30px;
                font-weight: bold;
                margin-bottom: 10px;
                margin-top: 5px;
                display: flex;
                align-items: center;
                &.level1 {
                    color: $color-level1;
                }
                &.level2 {
                    color: $color-level2;
                }
                &.level3 {
                    color: $color-level3;
                }
                &.level4 {
                    color: $color-level4;
                }
            }
            .desc {
                font-size: 12px;
                font-weight: bold;
                opacity: 0.5;
            }
            .title-ko {
                font-size: 15px;
                font-weight: bold;
            }
            .title-en {
                font-size: 15px;
                opacity: 0.7;
                margin-bottom: 5px;
                font-weight: bold;
            }
        }
    }

    .notice {
        color: #8f9191;
        font-size: 16px;
        // height: 24px;
        font-weight: 500;
        overflow: hidden;
        padding: 5px 10px;
        background-color: #333333;
        display: flex;
        align-items: center;
        .text-left {
            // float: left;
            flex-grow: 1;
            display: flex;
            align-items: flex-end;
            .bold {
                font-weight: bold;
                color: rgb(204, 204, 204);
            }
        }
        .text-right {
            display: flex;
            align-items: flex-end;
            flex: 0 0 auto;
        }
    }
}

.TabHotClip {
    .hot-clips {
        .filter-scroll {
            margin-left: -15px;
            width: 100vw;
            height: 40px;
            overflow-y: hidden;
            overflow-x: scroll;

            .filter-tags {
                padding: 0 15px;
                width: 600px;
                .tag {
                    display: inline-block;
                    font-size: 12px;
                    background-color: #333;
                    border-radius: 2px;
                    margin-right: 5px;
                    padding: 3px 8px;
                }
            }
        }

        .item-clip {
            &.today {
                border-bottom: 0 !important;
                margin-bottom: 80px;
                .play {
                    width: 70px !important;
                    height: 70px !important;
                    font-size: 25px !important;
                }
                .sentense {
                    font-size: 15px !important;
                    .active {
                        color: $color-brand;
                    }
                }
            }
            margin: 30px 0;
            border-bottom: 1px solid rgba(255, 255, 255, 0.062);
            .middle {
                display: flex;
                // justify-items: center;
                align-items: center;
                .play {
                    font-size: 15px;
                    width: 40px;
                    height: 40px;
                    border: 1px solid rgba(255, 255, 255, 0.342);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    text-align: center;
                    border-radius: 6px;
                    // padding-left: 3px;
                    margin-right: 10px;
                    color: #b7a6dd;
                    background-color: #4b465679;
                }
                .sentense {
                    font-size: 13px;
                    .active {
                        color: $color-brand;
                    }
                }
                .more {
                    color: #b7a6dd;
                    margin-left: auto;
                    .txt {
                        font-size: 13px;
                        margin-right: 10px;
                    }
                }
            }
            .bottom {
                font-size: 11px;
                margin-top: 5px;
                margin-bottom: 25px;
                padding-left: 50px;
                color: rgb(117, 117, 117);
                .bold {
                    color: $color-brand;
                    font-weight: bold;
                }
            }
        }
    }
}

.view-CourseMain {
    .section {
        padding: 5px 15px;
        &.item-container {
            padding-top: 60px; //calc(constant(safe-area-inset-top) + 60px);
            // padding-top: calc(env(safe-area-inset-top) + 60px);

            // padding-top: 50px;
        }
    }
    .topic-title {
        display: flex;
        align-items: center;
        margin-bottom: 30px;
        font-size: 30px;
        color: #fff;
        font-weight: 600;

        .i-right {
            margin-left: 20px;
        }
    }
    .item-topic {
        display: flex;
        width: 300px;
        margin-bottom: 25px;
        line-height: 19px;
        .cover-inapp {
            position: absolute;
            // background-color: #1d1e20db;
            // z-index: 2;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;

            .mark-inapp {
                color: $color-brand;
                font-size: 20px;
                box-shadow: 2px 2px 33px 6px rgba(0, 0, 0, 1);
            }
        }
        &.showed {
            img {
                opacity: 0.3;
            }
            .col-right {
                opacity: 0.5;
                .title-ko {
                    color: #d0d0d0;
                }
            }
        }
        .col-left {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 150px;
            .thumb {
                position: relative;
                .cnt {
                    padding: 0 3px;
                    font-size: 14px;
                    font-weight: bold;
                    display: inline-block;
                    color: #d0d0d0;
                    background-color: #4b4656;
                    position: absolute;
                    z-index: 1;
                }
                overflow: hidden;
                border-radius: 3px;
                width: 85px;
                height: 85px;
                img {
                    width: 85px;
                }
            }
        }

        .col-right {
            padding: 0 15px;
            width: 100%;
            font-size: 15px;
            letter-spacing: 0;
            display: inline-block;
            .desc {
                font-size: 1.1rem;
                color: #656565;
            }
            .title-ko {
                color: #4eb3dc;
            }
            .fa-heart {
                &.check {
                    color: #d53327;
                }
            }
        }
    }
}

.view-Topic {
    // background-color: #000;
    .TopicCover {
        // background-color: #c1d6c9;
        padding: 1.5rem;
        padding-top: 70px;
        color: $color-font-light;
        // background-color: #49454c;
        background-color: $color-header-bg2;
        background-repeat: no-repeat;
        background-size: cover;
        min-height: 180px;
        // padding-top: calc(constant(safe-area-inset-top) + 60px);
        // padding-top: calc(env(safe-area-inset-top) + 60px);
