            display: flex;
            width: 300px;
            margin-bottom: 1rem;
            .col-left {
                width: 40px;
                font-size: 30px;
                font-weight: bold;
                padding-top: 0;
                margin-top: -10px;
                line-height: 27px;
                .scene {
                    font-size: 19px;
                    opacity: 0.5;
                }
            }

            .col-right {
                line-height: 20px;
                padding: 0 15px;
                width: 100%;
                font-size: 15px;
                letter-spacing: 0;
                display: inline-block;
                .title-en {
                    // color: #dcdcdc;
                    font-weight: bold;
                    font-size: 18px;
                    margin-bottom: 0.5rem;
                }
                .title-ko {
                    // color: #c3c3c3;
                    font-size: 16px;
                    margin-bottom: 0.5rem;
                }
                .fa-heart {
                    &.check {
                        color: #d53327;
                    }
                }
            }
        }
        .desc {
            font-size: 1.4rem;
            color: #7e7e7e;
            font-weight: bold;
        }
    }
    .container-playlist {
        // background-color: #000;
        // padding: 0 1.5rem;
        margin-bottom: $play-control-height;
        // margin-bottom: $play-control-height;
        padding-bottom: calc(constant(safe-area-inset-bottom) + 6rem);
        padding-bottom: calc(env(safe-area-inset-bottom) + 6rem);
